body {
    width: 100%;
    height: 100%;
    font-family: "Lora","Helvetica Neue",Helvetica,Arial,sans-serif;
    color: $font-color;
    background-color: $primary-color;
    font-size: 10px;
    counter-reset: publication-item;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    text-transform: uppercase;
    font-family: "Montserrat","Helvetica Neue",Helvetica,Arial,sans-serif;
    font-weight: 700;
    letter-spacing: 1px;
}

/* Added in { Personal } */
h4 {
    a.category {
        color: inherit;
    }

    a.tag {
        font-size: 12px;
    }
}

a {
    color: $secondary-color;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    &:hover,
    &:focus {
        text-decoration: none;
        color: darken($secondary-color,20%);
    }
}

.navbar-custom {
    margin-bottom: 0;
    border-bottom: 1px solid rgba($font-color, 0.3);
    text-transform: uppercase;
    font-family: "Montserrat","Helvetica Neue",Helvetica,Arial,sans-serif;
    background-color: $navbar-sm-background-color;

    /* Fix height limit and scroll for small screen and many items menu */
    max-height: 100%;
    overflow-y: auto;

    .navbar-toggle {
        padding: 4px 6px;
        font-size: 16px;
        color: $navbar-color;
        &:focus,
        &:active {
            outline: none;
        }
    }
    .navbar-brand {
        font-weight: 700;
        &:focus {
            outline: none;
        }
    }
    a {
        color: $navbar-color;
        @if $header-text-shadow{
            text-shadow: 0 0 2px rgba(0, 0, 0, 0.8);
        }
    }
    .nav {
        text-align: center;
        li {
            a {
                -webkit-transition: background .3s ease-in-out;
                -moz-transition: background .3s ease-in-out;
                transition: background .3s ease-in-out;
                &:hover {
                    /* color: rgba($font-color, 0.8); */
                    outline: none;
                    background-color: rgba($font-color, 0.3);
                }
                &:focus,
                &:active {
                    outline: none;
                      background-color: rgba($font-color, 0.3);
                }
            }
            &.active {
                outline: none;
                a {
                    background-color: rgba($font-color, 0.3);
                    &:hover {
                        color: $navbar-color;
                    }
                }
            }
        }
    }
    @media(min-width:768px) {
        border-bottom: none;
        letter-spacing: 1px;
        background: $navbar-lg-background;
        -webkit-transition: background .5s ease-in-out,padding .5s ease-in-out;
        -moz-transition: background .5s ease-in-out,padding .5s ease-in-out;
        transition: background .5s ease-in-out,padding .5s ease-in-out;
    }
}


.row {
    display: flex;
    padding-left:15px;
}

.column {
    display:flex;
    flex-direction: column;
}

.content-section {
    width: 80vw;
    margin: 2.5rem 5rem;
    padding: 0;

    .publication-container {
        padding: 0;
        font-size: 1.6rem;
        margin-right: 2.5rem;

        .publication-list::before {
            counter-increment: publication-item;
            content: counter(publication-item) ".";
            padding-right: 1rem;
        }
    }

    .teaching-container {
        font-size: 1.6rem;
        margin-right: 2.5rem;
        .teaching-row {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
        }
        .teaching-column {
            display: flex;
            flex-direction: column;
            flex-basis: 100%;
            flex: 2;
        }

        .teaching-column-course {
            display: flex;
            flex-direction: column;
            flex-basis: 100%;
            flex: 4;
        }
        
        .teaching-column-term {
            display: flex;
            flex-direction: column;
            flex-basis: 100%;
            flex: 2;
        }
        .teaching-body {
            border-bottom: 1px solid rgba(0, 0, 0, .7);
            height: 3rem;
            transition: height 0.5s;
        }
        .teaching-header {
            font-weight: bold;
            background-color: rgb(230, 250, 250);
            border-bottom: 1px solid rgba(0, 0, 0, .7);
            height: 3rem;

        }


        @media(max-width:1100px) {
            .teaching-body {
                height: 3rem;

            }
        }
        @media(max-width:700px) {
            .teaching-body {
                height: 5rem;

            }
        }

        @media(max-width:500px) {
            .teaching-body {
                height: 7rem;

            }
        }
    }
    .profile-picture {
        width: 30rem;
    }
    .about-container {
        margin-top: 5rem;
        margin-right: 2.5rem;
        .about-row {
            display: flex;
            padding-left: 2.5rem;
        }

        .about-info {
            display: flex;
            flex-direction: column;
            justify-content: center;
            flex-grow: 1;
            margin-top: -1rem;
            margin-left: 6rem;
            font-size: 1.8rem;
            .about-info-item {
                margin-bottom: 2rem;
            }
            .address {
                display: flex;
                flex-direction: column;
            }
        }

        @media(max-width:768px) {
            .about-row {
                display: flex;
                flex-direction: column;
                padding-left: 0;
            }
            .about-info {
                display: flex;
                flex-direction: column;
                justify-content: center;
                flex-grow: 1;
                margin-top: 0rem;
                margin-left: 0;
                font-size: 1.8rem;
                .about-info-item {
                    margin-bottom: 2rem;
                }
                .address {
                    display: flex;
                    flex-direction: column;
                }
            }
        }

        .about-description{
            margin-top: 5rem;
            font-size: 1.6rem


        }
    }
}

.fourofour {
  border: 1px solid $primary-color !important;
}
